import { IVPAIDElement } from "../core/IVPAIDElement.js";

export class Text extends IVPAIDElement {
  constructor(data, element, vpaidSettings) {
    super(data, element, vpaidSettings);
    this.markLoaded();
  }

  createElement() {
    let cacheKey = this.getDomCacheKey();
    let domElement = this.getFromDomCache(cacheKey);
    if(!domElement) {
      const rootDivElement = this.createRootElement();
      const spanElement = document.createElement("span");
      spanElement.innerText =
      this.element.content || "Lorem ipSum Lorem ipSum";
      spanElement.style.fontSize = `${this.element.fontSize ? this.element.fontSize : 30}px`;
      spanElement.style.color = this.element.color || "black";
      spanElement.style.fontWeight = this.element.fontWeight || "normal";
      spanElement.style.backgroundColor =
        this.element.backgroundColor || "transparent";
      domElement = {root: rootDivElement, inline: spanElement, pIndices :[this.element.pIndex]};
      rootDivElement.appendChild(spanElement);
      this.setDomCache(cacheKey, domElement);
    } else {
      domElement.pIndices.push(this.element.pIndex);
    }
  }
}
